@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@500&display=swap');

  

body {
  background-color: #1d1e22; 
  color: #e5e5e5;
  background: linear-gradient(145deg, #1d1e22, #1f1f1f);
  font-family: 'Quicksand', sans-serif;
  font-size: 1.5rem;
  box-sizing: border-box;
}

#root {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

#drum-machine {
  background-color: #dfe4ea;
  background: linear-gradient(145deg, #484a51, #212121); 
  box-shadow: 5px 5px 10px #191919, -5px -5px 10px #282a31;
  border-radius: 20px;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;  
  padding: 0 20px;
}

.power {
  border-radius: 50%;
  width: 20px;
  height: 20px;
}

.on {
  background: linear-gradient(145deg, #05C46B, #026235);
  box-shadow: 5px 5px 9px #212121, -5px -5px 9px #484a51;
}

.off {
  background: linear-gradient(145deg, #FF6D6D, #FF1D1D);
  box-shadow: 5px 5px 9px #212121, -5px -5px 9px #484a51;
}

.bankOne {
  background: linear-gradient(145deg, #f7b731, #7b5b18);
  box-shadow: 5px 5px 9px #212121, -5px -5px 9px #484a51;
}

.bankTwo {
  background: linear-gradient(145deg, #3867d6, #1c336b);
  box-shadow: 5px 5px 9px #212121, -5px -5px 9px #484a51;
}

#display {
  text-align: center;
  padding: 5px 5px;
}

.drum-pads {
  padding: 5px;
  display: grid;
  grid: 75px 75px 75px / 75px 75px 75px;
  gap: 5px;
}

.drum-pad {
  background: linear-gradient(-145deg, #484a51, #212121);
  box-shadow: 5px 5px 9px #212121, -5px -5px 9px #484a51;
  background-color: #1a1a1a;
  border-radius: 50%;
  width: 80%;
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  justify-self: center;
  align-self: center;
}